@charset "UTF-8";
.ly-header {
  background: #eff1f3;
  animation-duration: 3s;
  animation-name: fadeIn; }

.svg-logoMark {
  position: relative;
  fill: #65cee9;
  width: 75%;
  opacity: 0.3;
  filter: drop-shadow(0 0px 3px rgba(0, 0, 0, 0.3));
  animation-duration: 1s;
  animation-name: slideIn;
  left: -100px; }

@keyframes slideIn {
  from {
    left: -200px;
    opacity: 0.1; }
  50% { }
  to {
    left: -100px;
    opacity: 0.3; } }

.bl-summary {
