.bl-pageNav {
  padding-top: 64px;
  padding-bottom: 64px; }
  .bl-pageNav_list {
    list-style: none;
    padding: 0;
    margin: 0;
    background: #fcfcfc; }
  .bl-pageNav_cnt {
    width: 100%;
